
import {
  defineComponent,
  ref,
  reactive,
  computed,
  watch,
  watchEffect,
  onBeforeMount,
  onMounted,
  onBeforeUnmount,
  onUnmounted,
  onBeforeUpdate,
  onUpdated,
} from 'vue'

export default defineComponent({
  name: 'Basic',
  setup(props, { attrs, slots, emit }) {
    console.log('props--', props)
    console.log('attrs--', attrs)
    console.log('slots--', slots)
    console.log('emit--', emit)

    const inputRef = ref<HTMLElement | null>()
    const obj = reactive({
      name: 'Dylan',
      age: 27,
      deposit: 100000000,
    })
    const value = ref(0)

    const computeVar = computed({
      get: () => obj.name + '_' + obj.age,
      set(val: string) {
        console.log(val)
        const [, age] = val.split('_')
        obj.age = Number(age) + 1
      },
    })

    watch(
      () => obj.age,
      (val: number) => {
        console.log(val)
      }
    )

    watch(
      value,
      (val: number) => {
        console.log(val)
      },
      {
        immediate: true,
      }
    )

    onMounted(() => {
      inputRef.value && (inputRef.value as HTMLElement).focus()
    })

    onBeforeMount(() => {
      console.log('--onBeforeMount')
    })

    onBeforeUnmount(() => {
      console.log('--onBeforeUnmount')
    })

    onUnmounted(() => {
      console.log('--onUnmounted')
    })

    onBeforeUpdate(() => {
      console.log()
    })

    onUpdated(() => {
      console.log()
    })
    return {
      obj,
      value,
      computeVar,
      inputRef,
    }
  },
})
